export default {
	data() {
		return {
			inputValue: ''
		}
	},
	methods: {
		startRecognize: function () {
			var options = {};
			var that = this;
			options.engine = 'iFly';
			that.inputValue = "";
			plus.speech.startRecognize(options, function (s) {
				console.log(s);
				that.inputValue += s;
			}, function (e) {
				console.log("语音识别失败：" + e.message);
			});
		},
		sendMessge () {
			var that = this;
			if(this.$store.state.isLogin){
				if (that.inputValue.trim() == '') {
					that.inputValue = '';
				} else {
				    console.log(that.inputValue);
					this.$emit("sendMessage",{inputValue:that.inputValue});
					that.inputValue = '';
				}
			 }else{
			 	uni.navigateTo({
			 		url: '../../../pages/login/login'
			       })
			}
		}
	}
}
